import { useState } from "react";
import { Box, Container, TextField, Button, Typography, Link } from "@mui/material";

export default function LoginPage() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [mensagem, setMensagem] = useState("");

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      const res = await fetch("http://localhost:8000/api/auth/login/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({
          username: username,
          password: password,
        }),
      });

      if (res.ok) {
        const data = await res.json();
        setMensagem("✅ Login realizado com sucesso!");
        console.log("Login OK:", data);
      } else {
        const error = await res.json();
        setMensagem(
          error?.non_field_errors?.[0] || "❌ Erro no login. Verifique as credenciais."
        );
      }
    } catch (err) {
      console.error(err);
      setMensagem("❌ Erro de rede ou servidor.");
    }
  };

  return (
    <Box display="flex" height="100vh">
      <Box
        flex={1}
        sx={{
          background: "linear-gradient(to bottom, #2196f3, #1e88e5)",
          color: "white",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Typography variant="h4" sx={{ fontWeight: "bold" }}>
          Conexão Hospitalar
        </Typography>
      </Box>

      <Box flex={1} display="flex" justifyContent="center" alignItems="center">
        <Container maxWidth="sm">
          <Typography variant="h4" gutterBottom>
            Faça seu login
          </Typography>

          {mensagem && (
            <Typography
              sx={{ color: mensagem.startsWith("✅") ? "green" : "red", mb: 2 }}
            >
              {mensagem}
            </Typography>
          )}

          <form onSubmit={handleLogin}>
            <Box mb={2}>
              <TextField
                label="Usuário"
                type="text"
                fullWidth
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
            </Box>
            <Box mb={2}>
              <TextField
                label="Senha"
                type="password"
                fullWidth
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </Box>
            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
              mb={2}
            >
              <Link href="/forgot-password" variant="body2" underline="hover">
                Esqueceu a senha?
              </Link>
              <Link href="/register" variant="body2" underline="hover">
                Cadastra-se
              </Link>
            </Box>
            <Button type="submit" variant="contained" color="primary" fullWidth>
              Login
            </Button>
          </form>
        </Container>
      </Box>
    </Box>
  );
}
